.header-top {
  display: flex;
  justify-content: space-between;
  max-width: 1411px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;

  @media (max-width: $tablet-width) {
    padding-left: 25px;
    padding-right: 25px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 17px;
    padding-right: 15px;
  }
}

.header-top__logo {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-template-rows: repeat(2, min-content);
  gap: 0 16px;
  grid-template-areas:
    "logo title"
    "logo subtitle";
}

.header-top__logo svg {
  grid-area: logo;
  align-self: center;
}

.header-top__logo h3 {
  grid-area: title;
  margin: 0;
  font-family: $custom-font-roboto;
  font-weight: 700;
  font-size: 25px;
  margin-top: 5px;
  color: $basic-white;
}

.header-top__logo p {
  grid-area: subtitle;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: $logo-grey;
}

@media (max-width: $mobile-width) {
  .header-top__logo h3,
  .header-top__logo p {
    display: none;
  }

  .header-top__logo svg {
    width: 38px;
    height: 38px;
  }
}

.header-top__contacts {
  display: flex;
  align-items: center;
}

.header-top__contacts-item {
  font-family: $custom-font-pt;
  font-weight: 400;
  font-size: 16px;
  text-decoration: none;
  color: $basic-white;
  transition: color 200ms;
}

.header-top__contacts-item svg {
  transform: scale(1);
  transition: transform 200ms;
}


.header-top__contacts-item span {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header-top__contacts-item--mail {
  margin-right: 88px;
  width: 135px;
  display: flex;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    width: 25px;
    margin-right: 33px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 23px;
  }
}

.header-top__contacts-item--mail svg {
  position: relative;
  top: 2px;

  @media (max-width: $tablet-width) {
    width: 25px;
    height: 19px;
    top: -3px;
  }
}

.header-top__contacts-item:hover {
  color: $light-blue;
}

.header-top__contacts-item:hover svg {
  transform: scale(1.3);
}

.header-top__contacts-item--phone {
  margin-right: 50px;
  width: 206px;
  display: flex;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    width: 24px;
    margin-right: 0;
  }
}

.header-top__contacts-item--phone svg {
  position: relative;

  @media (max-width: $tablet-width) {
    width: 24px;
    height: 24px;
    top: -3px;
  }
}

.header-top__contacts-call {
  width: 180px;
  border: 1px solid $basic-white;
  color: $basic-white;

  @media (max-width: $tablet-width) {
    position: absolute;
    left: -999px;
  }
}

.header-top__contacts-call:hover {
  background-color: $light-blue;
  border: 1px solid $light-blue;
}
